body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

footer {
  margin-top: 2em;
  font-size: 80%;
  text-align: center;
  width: 50%;
  margin: auto;
  color: white;
}
footer a {
  color: white;
}
body {
  background-color:  #5d5856; /*#464240;/*#282c34;*/
}

.navbar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.content-section {
  padding-top: 2em;
  margin: 2em auto auto auto;
  background: white;
  color:#5d5856;
  text-align: left;
  font-size: 15px;
  height: auto;
  width: 90%;
  box-shadow: 0px 0px 5px 5px #ffffff;  
  overflow:auto;
}

.content-section h1 {
  text-align: center;
  color: #353231;
  font-size: 3em;
}

.content-section h2 {
  text-align: center;
  color: #353231;
}



